          freightDetails: "",//
          collFreight: '',//
          collFreightBig: '',//
          serviceCode: '',//
          receipt: '',//
          totalMoney: '',//
          // userName:'',//
          stype: '',//

        },
        currentSale: 0,
        payCode: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        serviceCodes: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        rulesSend: {
          clientId: [{required: true, message: ':', trigger: 'blur'}]
        },

        //****
        //
        dialogVisitTitle: "",
        dialogVisitVisible: false,
        visit: {
          note: '',
          id: 0,
          billId: '',//id
          billstatus: '',//
        },
        visitRules: {}

        //*************
      }
    },
    mounted: function () {
      this.initData();
    },


    methods: {
      //**

      visitChange(index,row){

      },
      billstatus: function (data) {
        return data.billstatus === '1' ? '' : data.billstatus === '2' ? '' : '';
      },
      //
      returnVisit(row) {
        this.visit.billId = row.id;
        this.dialogVisitVisible = true;

      },
      addViist(addVisitForm) {
        let _this = this;
        let please = _.cloneDeep(this.visit);
        this.$refs[addVisitForm].validate((valid) => {
          if (valid) {
            if (please.id) {
              //
              this.tableLoading = true;
              console.log(_this.plan);
              this.postRequest("/returnvisit/add", please).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200 && resp.data.success) {
                  _this.dialogVisitVisible = false;
                  _this.loadData();
                  _this.cancelvisit();
                }
              })
            } else {
              //
              this.tableLoading = true;
              this.postRequest("/returnvisit/add", please).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200 && resp.data.success) {
                  _this.dialogVisitVisible = false;
                  _this.loadData();
                  _this.cancelvisit();
                } else {
                  Message("")
                }
              })
            }
          } else {
            return false;
          }
        });


      },
      cancelvisit() {
        this.visit = {
          note: '',
          id: 0,
          billId: '',//id
          billstatus: '',//
        }
      },
      //
      cancelSend() {
        this.confirmData = {
          id: 0,
          responseDetailsId: 0,//ID
          clientId: '',//id
          dispatchBillNo: '',//
          boxs: '',//
          consignee: '',//
          consigneePhone: '',//
          address: '',//
          consigner: '',//
          consignerPhone: '',//
          remarks: '',//
          cargoName: '',//
          freights: "",//
          shortHaulMoney: "",//
          freightDetails: "",//
          collFreight: '',//
          collFreightBig: '',//
          serviceCode: '',//
          receipt: '',//
          totalMoney: '',//
          // userName:'',//
          stype: '',//

        }
      },
      //
      confirmDelivery(index, row) {
        console.log('16a16541a65s4df65a');
        this.dialogSendTitle = '';
        this.confirmData.responseDetailsId = row.id;
        this.dialogSendVisible = true;
      },
      //
      confirmAdd(sendForm) {

        let _this = this;
        let please = _.cloneDeep(this.confirmData);
        please.clientId = please.clientId.pop();
        this.$refs[sendForm].validate((valid) => {
          if (valid) {
            if (please.id) {
              //
              this.tableLoading = true;
              console.log(_this.plan);
              this.postRequest("/shippingBill/add", please).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  _this.dialogSendVisible = false;
                  _this.loadData();
                  _this.showDetaillist()
                  _this.cancelSend();
                }
              })
            } else {
              //
              this.tableLoading = true;
              this.postRequest("/shippingBill/add", please).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200 && resp.data.success) {
                  _this.dialogSendVisible = false;
                  _this.loadData();
                  _this.cancelSend();
                } else {
                  Message("")
                }
              })
            }
          } else {
            return false;
          }
        });


      },
      /**
       * 
       * @param index
       * @param row
       */
      deliveryRequest(row) {
        this.dialogTitle = "";
        let that = this;
        this.CurrentSalesId = row.id;

        that.dialogFormVisible = true;
      },
      /**
       *  
       */
      showDetails(row) {
        let that = this;
        that.currentSale = row;
        that.detailsList = row.details;
        that.showDetaillist = true;
      },
      handleClick() {
        console.log(this.activePanel);
        switch (this.activePanel) {
          case "1":
            this.manageStatus = "4";
            break;
          case "2":
            this.manageStatus = "1";
            break;
          case "4":
            this.manageStatus = "2";
            break;
          default:
            break;
        }
        this.loadData();
      },      //


      formatStatus: function (row, column) {
        return row.materialType === 1 ? '' : '';
      },

      initData() {
        let _this = this;
        _this.loadData();
        this.getRequest("/productionplan/basedata").then(resp => {
          if (resp && resp.status === 200 && resp.data.success) {
            _this.clients = _this.toTree(resp.data.root.clients)
          } else {
            Message('@')
          }
        })

      },
      keywordsChange(val) {
        if (val === '') {
          this.loadData();
        }
      },
      keywordsMaterialChange(val) {
        if (val === '') {
          this.loadMaterial();
        }
      },
      showCurrentNo() {
        if (this.isprarent) {
          this.isprarent = false;
        } else {
          this.isprarent = true;
        }
      },


      currentChange(currentChange) {
        this.currentPage = currentChange;
        console.log(this.currentPage);
        this.loadData();
      },
      currentReChange(currentChange) {
        this.currentRePage = currentChange;
        console.log(this.currentRePage);
        this.loadData();
      },
      currentSeChange(currentChange) {
        this.currentSePage = currentChange;
        console.log(this.currentSePage);
        this.loadData();
      },
      currentVeChange(currentChange) {
        this.currentVePage = currentChange;
        console.log(this.currentVePage);
        this.loadData();
      },

      addAndFlushData() {
        this.dialogTitle = "";
        let that = this;
        that.dialogFormVisible = true;

      },
      searchData() {
        this.loadData();
      },
      add(addForm) {
        let _this = this;
        let detail = _.cloneDeep(this.detail);
        let please = {
          shippingRequestDetails: detail
        };
        this.$refs[addForm].validate((valid) => {
          if (valid) {
            if (_this.detail.id) {
              //
              this.tableLoading = true;
              console.log(_this.plan);
              this.postRequest("/shippingrequestdetails/update", please).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  // let data = resp.data;
                  _this.dialogFormVisible = false;
                  _this.loadData();
                  _this.emptyData();
                }
              })
            } else {
              //
              this.tableLoading = true;
              this.postRequest("/shippingrequestdetails/add?salesPlanId=" + this.CurrentSalesId, detail).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200 && resp.data.success) {
                  _this.dialogFormVisible = false;
                  _this.loadData();
                  _this.emptyData();
                } else {
                  Message("")
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      cancelOpt() {
        this.dialogFormVisible = false;
        this.loadData();
        this.emptyData();
      },

      deleteData(row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
        });
      },
      doDelete(ids) {
        this.tableLoading = true;
        let _this = this;
        this.getRequest("/sales/delete?id=" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200 && resp.data.success) {
            _this.loadData();
          } else {
            _this.$message({type: 'info', message: ''})
          }
        })
      },
      loadData() {
        let _this = this;
        this.tableLoading = true;
        switch (this.activePanel) {
          case "1"://
            this.getUrl = "/sales/findnotshipping?page=" + this.currentPage + "&size=10&status=" + this.manageStatus;
            break;
          case "2":// 
            this.getUrl = "/shippingrequest/findbypage?page=" + this.currentRePage + "&size=10";
            break;
          case "4"://
            this.getUrl = "/shippingBill/findbypage?page=" + this.currentSePage + "&size=10";
          case "5":
            this.getUrl='/returnvisit/findbypage?page='+this.currentVePage+'&size=10';
          default:
            break;
        }
        this.getRequest(this.getUrl)
          .then(
            resp => {
              this.tableLoading = false;
              if (resp && resp.status === 200 && resp.data.success) {
                _this.plans = resp.data.data;
                console.log(_this.plans);
                switch (this.activePanel) {
                  case "1": _this.totalCount = resp.data.total;break;
                  case "2": _this.totalReCount = resp.data.total;break;
                  case "4": _this.totalSeCount = resp.data.total;break;
                  case "5": _this.totalVeCount = resp.data.total;break;

                }
                if (_this.currentSale && _this.showDetaillist) {
                  _this.showDetails(_this.currentSale)
                }
                _this.emptyData();
              }
            })
      },
      emptyData() {
        this.CurrentSalesId = "";
        this.detail = {
          id: 0,
          planNumber: '',//
          destination: '',//
          receiveWay: '',//
          receiver: '',//
          relation: '',//
          note: '',//e
        };
      },
      back() {
        this.showDetaillist = !this.showDetaillist;
      },
      preDataSale(data) {
        if (data.length) {
          for (let i = data.length - 1; i >= 0; i--) {
            let e = data[i];
            let color = e.status + '';
            switch (color) {
              case '1':
                e.bgcolor = "#eeeeee";
                break;
              case '2':
                e.bgcolor = "yellowgreen";
                break;
              case '3':
                e.bgcolor = "green";
                break;
              case '4':
                e.bgcolor = "#e4e4e4";
                break;
              case '5':
                e.bgcolor = "#2974df";
                break;
              case '6':
                e.bgcolor = "#85ce61";
                break;
              default:
                break;
            }

          }
        }
        return data;
      },
      toTree(data) {
        let result = []
        if (!Array.isArray(data)) {
          return result
        }
        data.forEach(item => {
          delete item.child;
        });
        let map = {};
        data.forEach(item => {
          map[item.id] = item;
        });
        data.forEach(item => {
          let parent = map[item.parentClientId];
          if (parent) {
            (parent.child || (parent.child = [])).push(item);
          } else {
            result.push(item);
          }
        });
        console.log(result);
        return result;
      }
    },


  }

</script>
<style>

  .demo-table-expand label {
    width: 50px;
    color: #2974df;
  }

  .flex-send {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
  }

  .flex-send .item-send {
    display: block;
    box-sizing: border-box;
    height: 264px;
    width: 20%;
    overflow: hidden;
    /*margin-left: -4px;*/
    /*margin-top: -4px;*/
    /*line-height: 100px;*/
    text-align: center;
    list-style: none;
    padding: 5px;
    position: relative;
    justify-content: stretch;

  }

  .in-send-show:hover {
    border-color: red;
    position: relative;
  }

  .in-send-show {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 6px;
    margin-bottom: 5px;
    border: 1px solid #ccc;
    text-align: start;
    height: inherit;
    overflow-y: hidden;
    overflow: auto;
  }

  .send-input {
    width: 160px;
  }

</style>



